/* Global Styling */
@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@300;400;600&display=swap');

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: 'Poppins', sans-serif;
}

body {
  background: linear-gradient(to bottom, #f9f9f9, #e3e8ff);
  color: #333;
  line-height: 1.6;
}

/* Confirmation Container */
.confirmation-container {
  max-width: 400px;
  margin: 50px auto;
  padding: 30px;
  background: #ffffff;
  border-radius: 15px;
  box-shadow: 0 6px 15px rgba(0, 0, 0, 0.1);
  text-align: center;
}

.confirmation-container h2 {
  font-size: 2rem;
  color: #6c63ff;
  margin-bottom: 20px;
  font-weight: 600;
  text-transform: uppercase;
  letter-spacing: 1px;
}

.confirmation-container p {
  font-size: 1.2rem;
  color: #666;
  margin-bottom: 25px;
}

/* Button Styling */
button {
  width: 100%;
  background: #6c63ff;
  color: #fff;
  border: none;
  border-radius: 8px;
  padding: 12px 15px;
  font-size: 1rem;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.3s ease;
  margin-top: 15px;
}

button:hover {
  background: #5547e0;
  box-shadow: 0 4px 12px rgba(108, 99, 255, 0.4);
  transform: translateY(-2px);
}

/* Back to Home Button */
#home {
  background: #ff4d4d;
}

#home:hover {
  background: #e04444;
  box-shadow: 0 4px 12px rgba(255, 77, 77, 0.4);
}

/* Responsive Styling */
@media screen and (max-width: 480px) {
  .confirmation-container {
    padding: 20px;
  }

  .confirmation-container h2 {
    font-size: 1.5rem;
  }
}
